
.cartcontrol {
  font-size: 0;
}
.cartcontrol .cart-decrease {
    display: inline-block;
    padding: 6px;
    line-height: 24px;
    font-size: 24px;
    color: #00a0dc;
    -webkit-transition: all 0.4s ease;
    transition: all 0.4s ease;
}
.cartcontrol .cart-decrease.fadeRotate-enter-active, .cartcontrol .cart-decrease.fadeRatate-leave {
      opacity: 1;
      -webkit-transform: translate3d(0, 0, 0) rotate(0);
              transform: translate3d(0, 0, 0) rotate(0);
}
.cartcontrol .cart-decrease.fadeRatate-enter, .cartcontrol .cart-decrease.fadeRatate-leave-active {
      opacity: 0;
      -webkit-transform: translate3d(24px, 0, 0) rotate(180deg);
              transform: translate3d(24px, 0, 0) rotate(180deg);
}
.cartcontrol .cart-count {
    display: inline-block;
    vertical-align: top;
    width: 12px;
    padding-top: 6px;
    line-height: 24px;
    text-align: center;
    font-size: 14px;
    color: #000;
}
.cartcontrol .cart-add {
    display: inline-block;
    vertical-align: top;
    padding: 6px;
    line-height: 24px;
    font-size: 24px;
    color: #00a0dc;
}



